<template>
  <ckeditor
    v-model="value"
    :editor="ckeditor.editor"
    :config="ckeditor.config"
    @ready="onReady"
    @input="onChange"
    @blur="onBlur"
  />
  <template v-if="isEditorReady">
    <teleport to=".ck-toolbar__items">
      <slot name="toolbar"></slot>
    </teleport>
  </template>
</template>

<script>
import { computed, defineComponent, ref } from 'vue'

export default defineComponent({
  name: 'TextEditor',
  props: {
    options: {
      type: Object,
      default: () => ({ editor: {}, config: {} })
    },
    modelValue: {
      type: String,
      default: ''
    },
    withImageUpload: {
      type: Boolean,
      default: false
    },
    toolbarItems: {
      type: Array,
      default: () => []
    },
    onBlur: {
      type: Function,
      default: () => {}
    }
  },
  emits: ['update:modelValue'],
  setup(props, { emit }) {
    const isEditorReady = ref(false)
    const _editor = {}
    const ckeditor = computed(() => {
      const toolbar = {
        shouldNotGroupWhenFull: true,
        items:
          props.toolbarItems?.length > 0
            ? props.toolbarItems
            : [
                'undo',
                'redo',
                '|',
                'findAndReplace',
                'selectAll',
                '|',
                'link',
                'insertImage',
                'insertTable',
                'blockQuote',
                'mediaEmbed',
                'codeBlock',
                'pageBreak',
                'horizontalLine',
                'specialCharacters',
                'sourceEditing',
                '-',
                'heading',
                '|',
                'bold',
                'italic',
                'underline',
                'strikethrough',
                {
                  label: 'Basic styles',
                  icon: 'text',
                  items: [
                    'fontSize',
                    'fontFamily',
                    'fontColor',
                    'fontBackgroundColor',
                    'highlight',
                    'superscript',
                    'subscript',
                    'code',
                    '|',
                    'textPartLanguage',
                    '|'
                  ]
                },
                'removeFormat',
                '|',
                'alignment',
                '|',
                'bulletedList',
                'numberedList',
                'todoList',
                '|',
                'outdent',
                'indent'
              ]
      }
      if (!props.withImageUpload) {
        const index = toolbar.items.findIndex((item) => item === 'insertImage')
        if (index > -1) toolbar.items.splice(index, 1)
      }
      return {
        editor: props.options.editor,
        config: {
          ...props.options.config,
          removePlugins: ['Title', 'MediaEmbedToolbar'],
          width: '21cm',
          toolbar
        }
      }
    })
    const value = computed({
      get: () => props.modelValue || '',
      set: (value) => emit('update:modelValue', value)
    })
    const onReady = (editor) => {
      if (!!editor) {
        editor.ui.view.top._parentElement.classList.remove('ck-reset_all')
        editor.data.processor = editor.data.htmlProcessor
        isEditorReady.value = true
        Object.assign(_editor, editor)
        value.value = props.modelValue || ''

        editor.editing.view.document.on('error', (event, data) => {
          console.error('CKEditor view error:', data)
          onEditorError(editor)
        })
      }
    }
    const onEditorError = (editor) => {
      try {
        console.warn('Destroying CKEditor instance due to error...')
        editor.destroy()
        isEditorReady.value = false
      } catch (error) {
        console.error('Error destroying CKEditor instance:', error)
      }
    }
    const onChange = (e) => {
      emit('update:modelValue', e)
    }
    const forceSave = () =>
      new Promise((resolve) => {
        if (!Object.keys(_editor)) resolve(false)
        _editor.plugins.get('SourceEditing').isSourceEditingMode = false
        setTimeout(() => {
          resolve(true)
        }, 250)
      })

    return {
      value,
      isEditorReady,
      ckeditor,
      onReady,
      onChange,
      forceSave
    }
  }
})
</script>
<style lang="scss">
.ck-editor__editable {
  min-height: 300px;
}
.ck-editor__editable li {
  list-style-position: inside;
}
.ck-form__header__label {
  font-size: 13px !important;
}
</style>
